		                                            	<div class="my-group">
		                                                    <label>移除要素关键词：</label><br/>
		                                                    <textarea placeholder="添加需要从左侧报告列表中移除的要素" class="chBox chBox0"></textarea>
		                                                </div>
		                                                <div class="my-group" v-for="(item,i) in yaosu">
		                                                    <label>{{item}}：</label><br/>
		                                                    <div class="hidden ids">{{item|formatItem}}</div>
		                                                    <textarea placeholder="[添加要素关键词，单个词以逗号结尾，多个词以逗号分隔]" class="chBox" :class="'chBox'+(i+1)"></textarea>
