		<!--尾部-->
		<div class="footer">
		    <!--<p class="txt"><span><i class="my-place my-icon"></i>南京市秦淮区石杨路116号江苏省住建大厦A座6楼</span> &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;  <span><i class="my-phone my-icon"></i>86+025-84891479</span> &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;  <span><i class="my-email my-icon"></i>137669269@qq.com</span></p>-->
		    <!--<p class="txt"><span>Copyright © 大物移云数据科技有限公司 版权所有</span> &nbsp;  <span style="cursor: pointer" onclick=" window.open('http://www.miibeian.gov.cn');" >苏ICP备18014407号</span> &nbsp;  <span>网站建设：大物移云 南京</span></p>-->
		</div>
