</template>
<script type="text/javascript">
	import Login from './../login'
	import interBtn from './interactions'
	import amazeui from 'amazeui';
	require('amazeui/dist/css/amazeui.css');
	require('./image/commen.css');
	// import './image/jquery.easyui.min.js'
	import * as echarts from 'echarts';
	require('echarts-wordcloud');
	export default{
		name:'fenci',
		data(){
			return {
				yaosu : ['1 报告标题', '2 报告索引号', '3 收件人', '4 审计报告的范围','5 管理层的责任','6 注册会计师的责任','7 导致审计意见的事项','8 审计意见','8.1 标准无保留意见','8.2 保留意见','8.3 否定意见','8.4 无法表示意见','9 编制基础以及对分发和使用的限制','10 其他事项','11 强调事项','12 按照相关法律法规的要求报告的事项','13 注册会计师名称、地址、签字、盖章','14 报告日期']
			}
		},
		components:{
			Login,
			interBtn
		},
		filters:{
			formatItem(value){
				return value.substr(0,value.indexOf(" "));
			}
		},
		computed:{
			userName(){
				return sessionStorage.getItem('username');
			}
		},
		mounted: function(){
			 //        nav
		        $('.check-head .box .my-tabs-nav li').click(function () {
		            var num = $(this).index();
		            $(this).addClass('active').siblings('li').removeClass('active');
		            $('#doc-my-tabs .my-tabs-bd .my-tab-panel:nth-child(' + (num + 1) + ')').removeClass('hidden').siblings('.my-tab-panel').addClass('hidden');
		        });

		        // //cont-page的nav
		        // $(' .check-form .my-tabs .my-tabs-bd .my-inner-nav li a').click(function () {
		        //     var id=$(this).attr('title');
		        //     $(id).removeClass('hidden').siblings('.cont-page').addClass('hidden');
		        // });
		        let _this=this;
		//            上传文档
		        function fileVerificat(file){
		            if (file.indexOf("txt") != -1||file.indexOf("docx") != -1||file.indexOf("doc") != -1||file.indexOf("pdf") != -1){
		                return true;
		            } else {
		                return false;
		            }
		        }
		        $(".form-upload").each(function(){
		            var fileNames = $(this).find('#fileName');
		            $(this).find('.upload-up-btn').on("change","#paperFile",function(){
		                var filePath=$(this).val();
		                if(fileVerificat(filePath)){
		                    var arr=filePath.split('\\');
		                    var fileName=arr[arr.length-1];
		                    fileNames.val(filePath);
		                }else{
		                    fileNames.val("您未上传文件，或者上传文件类型有误").show();
		                    return false
		                }
		            });
		        })

		//        校验是否登录该页面
		        function checkLogin() {
		            function GetRequest() {
		                var url = location.search; //获取url中"?"符后的字串
		                var theRequest = new Object();
		                if (url.indexOf("?") != -1) {
		                    var str = url.substr(1);
		                    strs = str.split("&");
		                    for (var i = 0; i < strs.length; i++) {
		                        theRequest[strs[i].split("=")[0]] = decodeURIComponent(strs[i].split("=")[1]);
		                    }
		                }
		                return theRequest;
		            }
		            var a = GetRequest();
		            var bb=false;
		            if(a['username']){
		                console.log(a['username']);
		                bb=true;

		            }else {
		                alert('哇哦！你还没有登录呦！请点击右上角进行登录或注册');
		            }

		            return bb;
		        }
		        function getFileText(){
		            $('.fc-box ul').empty();
		            $('.cont-page .warp .tag ul').empty();
		            $('.cont-page .warp .marked ul').empty();
		            for(var i=1;i<=_this.yaosu.length;i++){
		            	$(".fc-box .form .chBox"+i).val('');
		            }
		            var files=document.getElementById("paperFile").files[0];
		            if(files){
		                //                            显示第二步隐藏第一步
		                $('.xinxi').html('');
		                $('.c-contain').removeClass('hidden');
		                $('.check-head .box .my-tabs-nav li:nth-child(2)').addClass('active').siblings('li').removeClass('active');
		                $('#doc-my-tabs .my-tabs-bd .my-tab-panel:nth-child(2)').removeClass('hidden').siblings('.my-tab-panel').addClass('hidden');


		                var reader = new FileReader();//new一个FileReader实例
		                //判断文件类型，是不是text类型
		                if (/text+/.test(files.type)||/application+/.test(files.type)) {
		                    //                            显示第一步隐藏第二步
		                    $('.c-contain').removeClass('hidden');
		                    if(/application+/.test(files.type)){
		                        reader.readAsText(files, "gb2312");
		                    }else{
		                        reader.readAsText(files, "gb2312");
		                    }

		                    reader.onload = function() {
		                        console.log(this.result.toString());

		                        var filesText=this.result;

		                        cctitle(filesText);

		                    }
		                }else{
		                    alert('请上传文档');
		                }
		            }else{
		                alert('请上传文档');
		            }

		            function cctitle(filesText){
		                $.ajax({
		                    url: "http://121.42.29.188:10000/zhuajiacihui_fenci",
		                    type: 'post',
		                    async: true,
		                    dataType: "json",
		                    data: {
		                        "text": filesText,
		                    },
		                    success: function (result) {
		                        console.log(result.data);
		                        for(var i=0;i<result.data.length;i++){
		                            if(result.data[i].value>5){
		                                $('.fc-box ul').append('<li class="dragitem cf-word color5">'+result.data[i].name+'</li>');
		                            }else if(result.data[i].value<=0){
		                                $('.fc-box ul').append('<li class="dragitem cf-word color0">'+result.data[i].name+'</li>');
		                            }else{
		                                $('.fc-box ul').append('<li class="dragitem cf-word color'+(result.data[i].value-1)+'">'+result.data[i].name+'</li>')
		                            }

		                        }


		                        drag();
		                    }
		                });
		            }


		            function str_ch(color) {
		                for(var i=0;i<color.length;i++){
		                    if(color[i]==""){
		                        $('.fc-box ul .color'+i).css('background-color',"transparent");
		                    }else{
		                        $('.fc-box ul .color'+i).css('background-color',color[i]);
		                    }

		                }
		            }

		            var n=100;
		            var a=9;
		            var _move=false;//移动标记
		            var _x,_y;//鼠标离控件左上角的相对位置
		            $(".drag").click(function(){
		                //alert("click");//点击（松开后触发）
		            }).mousedown(function(e){
		                _move=true;
		                _x=e.pageX-parseInt($(".drag").css("left"));
		                _y=e.pageY-parseInt($(".drag").css("top"));
		                $(".drag").fadeTo(20, 0.5);//点击后开始拖动并透明显示
		            });

		            var x;
		            $(document).mousemove(function(e){
		                if(_move){
		                    x=e.pageX-_x;//移动时根据鼠标位置计算控件左上角的绝对位置
		                    if(x<=n/2){
		                        x=0;
		                    }else if(x>=(6*n-n/2)){
		                        x=n*6-a;
		                    }

		                    $(".drag").css({top:-8,left:x});//控件新位置
		                }
		            }).mouseup(function(){
		                _move=false;
		                var color=["#e0e0e0", "#d1d1d1", "#99ccff", "#7fbfff", "#ff9933", "#ff7733"]
		                if(x<n/2){
		                    x=0-a;
		                    color=["","","", "", "", ""];
		                    console.log(color);
		                    str_ch(color)
		                }
		                else if(x>=n/2&&x<(2*n-n/2)){
		                    x=n-a;
		                    color=["", "", "", "", "", "#ff7733"];
		                    console.log(color);
		                    str_ch(color)
		                }else if(x>=(2*n-n/2)&&x<(3*n-n/2)){
		                    x=n*2-a;
		                    color=["", "", "", "", "#ff9933", "#ff7733"]
		                    console.log(color);
		                    str_ch(color)
		                }else if(x>=(3*n-n/2)&&x<(4*n-n/2)){
		                    x=n*3-a;
		                    color=["", "", "", "#7fbfff", "#ff9933", "#ff7733"]
		                    console.log(color);
		                    str_ch(color)
		                }else if(x>=(4*n-n/2)&&x<(5*n-n/2)){
		                    x=n*4-a;
		                    color=["", "", "#99ccff", "#7fbfff", "#ff9933", "#ff7733"]
		                    console.log(color);
		                    str_ch(color)

		                }else if(x>=(5*n-n/2)&&x<(6*n-n/2)){
		                    x=n*5-a;
		                    color=["", "#d1d1d1", "#99ccff", "#7fbfff", "#ff9933", "#ff7733"]
		                    console.log(color);
		                    str_ch(color)
		                }else if(x>=(6*n-n/2)){
		                    x=n*6-a;
		                    color=["#e0e0e0", "#d1d1d1", "#99ccff", "#7fbfff", "#ff9933", "#ff7733"]
		                    console.log(color);
		                    str_ch(color)
		                }
		                $(".drag").css({top:-8,left:x});//控件新位置
		                $(".drag").fadeTo("fast", 1);//松开鼠标后停止移动并恢复成不透明

		            });
		//        拖拽事件
		            function drag() {
		                $('.dragitem').draggable({
		                    revert:true,
		                    proxy:'clone',
		                    onStartDrag:function(){
		                        $(this).draggable('options').cursor = 'not-allowed';
		                        $(this).draggable('proxy').css('z-index',999);
		                    },
		                    onStopDrag:function(){
		                        $(this).draggable('options').cursor='move';
		                    }
		                });

		             //    for(var i=1;i<=20;i++){
			            // 	$('.chBox'+i).droppable({
			            //         onDragEnter:function(e,source){
			            //             $(source).draggable('options').cursor='auto';
			            //         },
			            //         onDragLeave:function(e,source){
			            //             $(source).draggable('options').cursor='not-allowed';
			            //         },
			            //         onDrop:function(e,source){
			            //             var name = $(source).html();
			            //             addItem(name,i);
			                       
			            //         }
			            //     });
			            //     return;
			            // }
		                $('.chBox').droppable({
		                    onDragEnter:function(e,source){
		                        $(source).draggable('options').cursor='auto';
		                    },
		                    onDragLeave:function(e,source){
		                        $(source).draggable('options').cursor='not-allowed';
		                    },
		                    onDrop:function(e,source){
		                    	// console.log($(this)[0].classList[1]);
		                    	var id=$(this)[0].classList[1];
		                        var name = $(source).html();
		                        addItem(name,id);
		                        if(id==='chBox0'){
		                        	// $(source).removeClass();
		                        	$(source).css('display','none');
			                    	// $(source).empty();
		                        }
		                    }
		                });
		                function addItem(name,id) {
		                    var text=$('.'+id).val();
		                    $('.'+id).val(text+name+',')
		                }
		            }
		        }
		        $('.fc-box .form .submit').click(function () {
	                var reg=new RegExp("，","g");
	                var chBoxArr={};
	                var word2=$(".fc-box .form .chBox0").val()?$(".fc-box .form .chBox0").val():',';
	                for(var i=1;i<=_this.yaosu.length;i++){
	                	var word=$(".fc-box .form .chBox"+i).val();
	                	if(word){
	                		word=word.replace(reg,",");
	                		if(word.search(",")<0){
	                			word=word+","
	                		}else{
	                			word=word
	                		}
	                	}else{
	                		word=''
	                	}
	               	   var key=$(".fc-box .form .chBox"+i).siblings('div.ids').html();
	               	   chBoxArr[key]=word;
	                }
	                $.ajax({
                        url: "http://121.42.29.188:10000/zhuajiacihui_yaosucihuicunchu",
                        type: 'post',
                        dataType: "json",
                        data: {
                        	"username":_this.userName,
                        	"qiye_id":'yaosu',
                        	"feilei_id":'yaosu',
                        	"yidian_name":'cihui_yaosu',
                        	"word2":word2,
                            "words": JSON.stringify(chBoxArr)
                        },
                        success: function (result) {
                            console.log(result);
                            if(result.message==1){
                            	alert('提交成功');
                            	$(".fc-box .form .chBox").val('');
                            }
                        }
                    })
	                console.log(chBoxArr);
	            })
		        $('#submit .img1').click(function () {
		        	if(_this.userName){
		        		console.log(_this.userName);
		        		getFileText();
		        	}else{
		        		alert("请先登录该系统");
		        	}
		        });
		}
	}
</script>
<style>
	 /*fc-box样式*/
	.yaosu .tag ul{
		overflow: hidden;
	}
	.yaosu .tag,.marked{
		width: 80%;
		margin: 0 auto;
		padding:2%;
		border: 1px solid #ccc;
		margin-bottom: 20px;
	}
	.yaosu .marked ul{
	    position: relative;
	    width: 100%;
	    margin: 0 auto;
	    max-height: 500px;
	    overflow: auto;
	    padding: 1%;
	    box-sizing: border-box;
	}
	.yaosu .fc-box{
		overflow: hidden;
		/*width: 80%;
		margin: 0 auto;*/
		min-height: 500px;
		padding:2%;
		border: 1px solid #ccc;
		margin-bottom: 20px;
		box-sizing: border-box;
	}
	.yaosu .fc-box ul{
	    position: relative;
	    max-height: 500px;
	    overflow: auto;
	    /*padding: 50px;*/
	    padding-right: 30%;
	    box-sizing: border-box;
	}

	.yaosu .fc-box li{
	    cursor: pointer;
	    float: left;
	    margin: 3px 5px;
	    border-radius:4px;
	    padding:2px 4px;
	    color: #000;
	    font-size: 15px;
	}
	/*color=["#e0e0e0","#d1d1d1","#99ccff","#7fbfff","#ff9933","#ff7733"];*/
	.yaosu .fc-box .color0{
	    background-color: #e0e0e0;
	}
	.yaosu .fc-box .color1{
	    background-color: #d1d1d1;
	}
	.yaosu .fc-box .color2{
	    background-color:#99ccff ;
	}
	.yaosu .fc-box .color3{
	    background-color:#7fbfff ;
	}
	.yaosu .fc-box .color4{
	    background-color: #ff9933;
	}
	.yaosu .fc-box .color5{
	    background-color: #ff7733;
	}
	.yaosu .fc-box .my-group .chBox{
	    width: 100%;
	    height: 100px;
	}
	.yaosu .fc-box .addBox{
		position:absolute;
		top:0;
		right:28px;
		max-height: 450px;
		overflow-y: overlay;
	}

	/*此行类别CSS*/
	.yaosu #two .warp li{
	    float: left;
	    font-size: 16px;
	    color: #000;
	    width: auto;
	    height: 24px;
	    padding: 5px 8px;
	    margin: 8px;
	    border-radius: 6px;
	}
	.yaosu #two .warp .marked li{
	    font-size: 15px;
	    color: #000;
	    width: auto;
	    float: left;
	    margin: 3px 5px;
	    border-radius: 4px;
	    padding: 2px 4px;
	}
	.yaosu .yc-box {
	    width: 100%;
	    height: 140px;
	    /*line-height: 85px;*/
	    background-color: #fff;
	    box-shadow: 0px 5px 6px 2px rgba( 0, 0, 0 ,0.1);
	}
    .yaosu .banner{
        width: 100%;
        height: 140px;
        /*line-height: 140px;*/
        text-align: center;
    }
    .yaosu .banner .img-title{
        display: inline-block;
        width: 394px;
        height: 85px;
        margin-left: 20%;
        background: url(image/title2.png) no-repeat;
        background-position: 0 90%;
        margin-top: 28px;
    }
    .yaosu .am-container{
        max-width: 1500px;
        box-sizing: border-box;
    }
    .yaosu .am-u-md-9 {
        width: 70%;
    }
    .yaosu .am-collapse {
        display: none;
        max-height: 500px;
        overflow-y: auto;
    }
    .yaosu .check-head{
        margin: 60px auto;
    }
    .yaosu .check-head .startBtn{
        background:url(image/check.png) no-repeat center;
    }
    .yaosu .check-head .downloadBtn{
        background:url(image/download.png) no-repeat center;
    }
    .yaosu .panel-phone{
		width:60%!important;
		margin:0 auto
	}
	 @media only screen and (max-width: 1024px){
		.yaosu .panel-phone{
			width:90%!important;
			margin:0 auto
		}
	}
    .yaosu .check-form .my-tabs{
        width: 100%;
        min-height: 700px;
        padding: 30px 60px;
        box-sizing: border-box;
        text-align: justify;
        /*border: 2px solid #0fb3dc;*/
        border-radius: 8px;
        background-color: #fff;
        box-shadow: 0 1px 6px rgba(0,0,0,0.4);

    }
    .yaosu .check-head .box .my-tabs-nav{
        margin: 0 auto;
        width: 70%;
        overflow: hidden;
        height: 40px;
        line-height: 40px;
        text-align: center;
        background: url("image/nav-bg.png") no-repeat center;
        background-size: cover;

    }
    .yaosu .check-head .box .my-tabs-nav li{
        float: left;
        width: 50%;
        text-align: center;
        color: #fff;
        cursor: pointer;
    }
    .yaosu .check-head .box .my-tabs-nav li.active{
        color: #333;
    }
    .yaosu .check-form .my-tabs .my-tabs-bd{
        position: relative;
        width: 100%;
        min-height: 466px;
        margin: 0 auto;
        margin-top: 55px;
        padding: 40px 0;
        box-sizing: border-box;
    }
    .yaosu .check-form .my-tabs .my-tabs-bd .my-inner-nav{
        position: absolute;
        top:-130px;
        left: 50%;
        margin-left: -140px;
    }
    .yaosu .check-form .my-tabs .my-tabs-bd .my-inner-nav li{
        float: left;
        margin: 0 20px;
    }
    .yaosu .check-form .my-tabs .my-tabs-bd .my-inner-nav li a{
        display: block;
        width: 100px;
        height: 100px;
        border-radius: 50px;
        box-shadow: 0px 5px 6px 2px rgba( 0, 0, 0 ,0.1);
        cursor: pointer;
        /*border: 1px solid #000;*/

    }
    .yaosu .check-form .my-tabs .my-tabs-bd .my-inner-nav li:nth-child(2) a {
        background: #ff9090 url(image/1.jpg) no-repeat center;
    }
    .yaosu .check-form .my-tabs .my-tabs-bd .my-inner-nav li:nth-child(1) a {
        background: #ffcf90 url(image/2.jpg) no-repeat center;
    }
    .yaosu .check-form .my-tabs .my-tabs-bd .form .form-group{
        position: relative;
        margin-bottom: 20px;
    }
    .yaosu .check-form .form .form-group .form-label{
        position: absolute;
        font-weight: normal;
        width: 100px;
        left: 0;
        top:12px;
        font-size: 18px;
        color: #4c4c4c;
        text-align: left;
        font-family: "微软雅黑";
    }
    .yaosu .check-form .form .form-group .form-input{
        width: 100%;
        padding-left: 100px;
        box-sizing: border-box;

    }
    .yaosu .check-form .form .form-group .form-input input,
    .yaosu .check-form .form .form-group .form-input select{
        width: 100%;
        height: 44px;
        line-height: 44px;
        font-size: 15px;
        padding: 0;
        padding-left: 14px;
        /*border: 1px solid #dedede;*/
        /*outline: none;*/
    }
    .yaosu .check-form .form .form-group .form-input .file-name {
        float: left;
        width: 78%;
        height: 44px;
        cursor: pointer;
        border: 1px solid #dfdfdf;
        padding-left: 15px;
        box-sizing: border-box;
    }
    .yaosu .check-form .form .form-group .form-input .upload-up-btn {
        float: left;
        width: 20%;
        height: 44px;
        background: #b80000;
        color: #ffffff;
        text-align: center;
        position: relative;
        line-height: 44px;
        margin-left: 2%;
        font-size: 14px;
        -webkit-border-radius: 3px;
        -moz-border-radius: 3px;
        border-radius: 3px;
        border: none;
        cursor: pointer;
        /*margin-right: 10px;*/
    }
    .yaosu .check-form .form .form-group .form-input .upload-up-btn input[type="file"] {
        width: 100%;
        height: 44px;
        padding: 0;
        opacity: 0;
        cursor: pointer;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
    }
    .yaosu .check-form .form .form-group .form-input .clearfix:after {
        clear: both;
    }
    .yaosu .check-form .form .form-group .form-input .clearfix:after, .clearfix:before {
        content: "\0020";
        display: block;
        height: 0;
        overflow: hidden;
    }
    /*  修改日历控件类型 */
    ::-webkit-datetime-edit { padding: 1px;}  /*控制编辑区域的*/
    ::-webkit-datetime-edit-fields-wrapper { background-color: #fff; }    /*控制年月日这个区域的*/
    ::-webkit-datetime-edit-text { color: #333; padding: 0 .5em; }  /*这是控制年月日之间的斜线或短横线的*/
    ::-webkit-datetime-edit-year-field { color: #333; }    /*控制年文字, 如2013四个字母占据的那片地方*/
    ::-webkit-datetime-edit-month-field { color: #333; }    /*控制月份*/
    ::-webkit-datetime-edit-day-field { color: #333; }    /*控制具体日子*/
    ::-webkit-inner-spin-button { visibility: hidden; }    /*这是控制上下小箭头的*/
    ::-webkit-calendar-picker-indicator {      /*这是控制下拉小箭头的*/
        /*border: 1px solid #ccc;*/
        border-radius: 2px;
        /*box-shadow: inset 0 1px #fff, 0 1px #eee;*/
        /*background-color: #eee;*/
        /*background-image: -webkit-linear-gradient(top, #f0f0f0, #e6e6e6);*/
        /*color: #666;*/
    }
    .yaosu #accordion .am-panel .am-panel-hd .panel-bs{
        color: #737373;
        font-size: 15px;
        text-align: right
    }
    .yaosu #accordion .am-panel .am-panel-hd .panel-bs .bs{
        cursor: pointer;
    }
    .yaosu #accordion .am-panel .am-panel-hd .panel-bs .bs:hover{
        text-decoration: underline;
    }
    .yaosu #accordion .am-panel .am-panel-hd .panel-bs .bs.click{
        color:#00a2c9;
    }
    .yaosu .am-panel-group .am-panel-bd{
        word-wrap: break-word;
        -webkit-hyphens: auto;
        -ms-hyphens: auto;
        hyphens: auto;
    }
    .yaosu .am-panel-group .am-panel-bd p{
        line-height: 32px;
        font-size: 15px;
        word-spacing: 4px;
    }
    .yaosu #accordion2 .am-panel-bd p:hover{
        background-color: #e6fcfe;
    }
    .yaosu .am-panel-group .am-panel-bd i{
        font-style:normal;
    }

    .yaosu .btn-group-find{
        text-align: center;
    }
    .yaosu .btn-group-find .btn-find{
        display: inline-block;
        width: 200px;
        height: 44px;
        line-height: 44px;
        background-color: #e5f4ff;
        font-size: 22px;
        text-align: center;
        color: #b80000;
        border-radius: 5px;
        border: 1px solid  #b80000;
        margin: 0 10px;
        cursor: pointer;
    }
    .yaosu .btn-group-find .btn-find.light{
        background-color: #b80000;
        color: #fff;
    }



    /*词云弹出框*/
    .yaosu #doc-modal-cy .am-modal-dialog{
        width: 1000px;
        height: 700px;
    }
    .yaosu #doc-modal-cy .list-cy{
        width: 80%;
        margin: 0 auto;
        height: 44px;
        line-height: 44px;
        overflow: hidden;
    }
    .yaosu #doc-modal-cy .list-cy li{
        float: left;
        width: 33.33%;
        height: 100%;
        text-align: center;
        font-size: 22px;
        color: #333;
        cursor: pointer;
    }
    .yaosu #doc-modal-cy .list-cy li.active{
        color: #b80000;
    }
    .yaosu #doc-modal-cy .modal-content .cont{
        width: 900px;
        height: 600px;
        margin: 0 auto;
    }
    .yaosu #accordion .am-panel .cyBtn{
        cursor: pointer;
        color:#ff5e00;
    }
    .yaosu #accordion .am-panel .cyBtn:hover{
        text-decoration: underline;
    }

    .yaosu .color-img .color-box{
        position: relative;
        display: inline-block;
        width: 600px;
        height: 6px;
        /*margin-top: 80px;*/
        background: url("image/color_xsd.png");
        /*border:1px solid #0f9ae0;*/

    }
    /*模块拖拽*/
    .yaosu .color-img .drag{
        position:absolute;
        width:20px;
        height:22px;
        /*border:1px solid #ddd;*/
        background:url("image/drag_xsd.png") no-repeat;
        background-size: contain;
        top:-8px;
        left:591px;
        cursor:pointer;
    }
    .yaosu .color-img .color-font{
        vertical-align: middle;
        font-size: 22px;
        font-weight: bold;
        margin:0 10px;
    }
    .yaosu .am-u-md-3 {
	    box-sizing: border-box;
	}
</style>


